import React, { useState } from "react";

const TableTabs = ({ tabletabs ,type}) => {
  return (
    <ul className="nav nav-tabs leaveTabs mb-4">
      {tabletabs.map((tab, index) => (
        <li className="nav-item" key={tab.id}>
          <button
            className={`nav-link ${index == 0 ? "active" : null}`}
            data-bs-toggle="tab"
            data-bs-target={`#${tab?.id}`}
            type="button"
            onClick={()=>tab.onClickFun(tab.id,type)}
          >
            {tab.name}
          </button>
        </li>
      ))}
    </ul>
  );
};

export default TableTabs;
