import React, { useEffect, useState } from 'react'
import { CustomTabs } from '../reuseableComponents'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction } from '../../core/resource/actions'
import { CustomSpinner } from '../globalComponents'
import BlankTableError from '../commanComponent/BlankTableError'
import { getBadgeColor } from '../../utils/common'

const LeaveToday = () => {
    const dispatch = useDispatch()
    const {

        getLeaveTodayPending,
        getLeaveTodayRes,
        getLeaveTodayError,

    } = useSelector(state => state.leave)

    const [leaveTodayData, setLeaveTodayData] = useState('')
    const leaveTodayTabsRequest = [
        { name: "Full Day", onClickFun: (id) => getLeaveOnToday(id), id: "full_day" },
        { name: "First Half", onClickFun: (id) => getLeaveOnToday(id), id: "first_half" },
        { name: "Second Half", onClickFun: (id) => getLeaveOnToday(id), id: "second_half" },
    ]
    useEffect(() => {
        getLeaveOnToday()
    }, [])
    const getLeaveOnToday = (leaveType) => {
        const payload = {
            status: leaveType || "full_day"
        }
        dispatch(leaveAction.getLeaveTodayRequest(payload))
    }

    useEffect(() => {
        if (!getLeaveTodayPending) {
            if (getLeaveTodayError) {
                return;
            }
            if (getLeaveTodayRes) {
                if (getLeaveTodayRes?.code === 200) {
                    setLeaveTodayData(getLeaveTodayRes?.data)
                }
            }
        }
    }, [
        getLeaveTodayRes,
        getLeaveTodayError,
        getLeaveTodayPending
    ]);

    const renderLeavedEmployeeList = () => {
        return (
            
            leaveTodayData &&leaveTodayData?.length>0? leaveTodayData?.map((item,ind) => (      
                <li>
                    <div className="profile d-flex align-items-center justify-content-between">
                        <div className="d-flex align-items-center gap-2">
                            <figure className="m-0">
                                <img src="images/profile.png" alt="" />
                            </figure>
                            <span>{item?.name}</span>
                        </div>
                        <strong className={getBadgeColor(item?.is_planned_leave?'planned':"unplanned")}>{item?.is_planned_leave?"Planned":"Unplanned"}</strong>
                    </div>
                </li>
            )):(
                !getLeaveTodayPending &&<div className='d-flex justify-content-center'> <BlankTableError  /></div>
            )
        )
    }
    return (
        <>

            <div className="cardBox h-100">
                <div className="cardHead">
                    <h3>Who's on Leave Today</h3>
                </div>
                <CustomTabs tabs={leaveTodayTabsRequest} type="leaveToday" />
                <div className="tab-content">
                    <div className="tab-pane fade active show" id="full_day">
                        <ul className="innercontent">
                            {getLeaveTodayPending ?
                                <div>
                                    <CustomSpinner isShow={getLeaveTodayPending} size="xl" />
                                </div> : renderLeavedEmployeeList()}
                        </ul>
                    </div>
                    <div className="tab-pane fade" id="first_half"
                        role="tabpanel">
                        <ul className="innercontent">
                        {getLeaveTodayPending ?
                                <div>
                                    <CustomSpinner isShow={getLeaveTodayPending} size="xl" />
                                </div> : renderLeavedEmployeeList()}
                        </ul>
                    </div>
                    <div className="tab-pane fade" id="second_half" role="tabpanel">
                        <ul className="innercontent">
                        {getLeaveTodayPending ?
                                <div>
                                    <CustomSpinner isShow={getLeaveTodayPending} size="xl" />
                                </div> : renderLeavedEmployeeList()}
                        </ul>
                    </div>
                </div>
            </div>

        </>
    )
}

export default LeaveToday
